@import "../../styles/variables.scss";

.footer {
  display: flex;
  flex-direction: column;
  opacity: 0.7;
  text-align: center;
  row-gap: 10px;
  padding-top: 300px;
  padding-bottom: 200px;

  p {
    margin: 0;
    // font-size: calc(16px / 1440px * 100vw);
    line-height: 23px;
  }
}

@media screen and (max-width: $md), (orientation: portrait) {
  .footer {
    p {
      margin: 0;
      font-size: calc(16px / $md * 100vw);
    }
  }
}

@media screen and (max-width: $xs) {
  .footer {
    p {
      margin: 0;
      font-size: 14px;
    }
  }
}
