@import "../../styles/variables.scss";

.scenography {
  height: 200vh;
  height: 200svh;
  margin-top: 50vh;
  margin-top: 50svh;
  margin-bottom: 50vh;
  margin-bottom: 50svh;

  .inner {
    position: sticky;
    top: 50%;
    perspective: 1000px;
    transform-style: preserve-3d;
  }

  h1 {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-transform: uppercase;
    font-weight: normal;
    font-size: calc(128px / 1440px * 100vw);
    margin: 0;
    white-space: nowrap;
    z-index: 2;
    overflow: hidden;
    letter-spacing: calc(-4px / 1440px * 100vw);

    span {
      display: inline-block;
      // transform: translateY(85%);
      &[data-rotation] {
        // display: inline-block;
        // transform: translateY(30%);
        // transform: translateY(0%);
      }
    }
  }

  .imgWrapper {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 100%;
    height: 100vh;
    height: 100svh;

    .arrowRight {
      mix-blend-mode: difference;
      position: absolute;
      right: 15px;
      bottom: 0;
      top: 0;
      margin: auto;
      width: 15px;
      height: 15px;
      // background-color: white;
      border-top: solid 2px white;
      border-right: solid 2px white;
      transform: rotate(45deg);
      z-index: 10;
      cursor: pointer;
    }

    .arrowLeft {
      mix-blend-mode: difference;
      position: absolute;
      left: 15px;
      bottom: 0;
      top: 0;
      margin: auto;
      width: 15px;
      height: 15px;
      // background-color: white;
      border-bottom: solid 2px white;
      border-left: solid 2px white;
      transform: rotate(45deg);
      z-index: 10;
      cursor: pointer;
      transition: opacity 0.5s;
    }

    .arrowHidden {
      opacity: 0;
      pointer-events: none;
    }

    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
      // width: 100%;
      // height: 100%;
      // position: absolute;
      // top: 0;
      // left: 0;
      // opacity: 0;
    }
  }
}

@media screen and (max-width: $md), (orientation: portrait) {
  .scenography {
    h1 {
      font-size: calc(90px / $md * 100vw);
      letter-spacing: calc(-2px / $md * 100vw);
    }

    .inner {
      top: 45%;
    }

    .imgWrapper {
      height: 50vh;
      height: 50svh;
    }
  }
}

@media screen and (max-width: $xs) {
  .scenography {
    h1 {
      font-size: calc(40px / 375px * 100vw);
      letter-spacing: calc(-2px / 375px * 100vw);
    }
  }
}
