@import "../../styles/variables.scss";

.colorsAndFabric {
  margin-top: -50vh;
  margin-top: -50svh;
  // position: relative;

  .intro {
    max-width: calc(240px / 1440px * 100vw);
    margin-left: calc(1020px / 1440px * 100vw);
    margin-bottom: calc(-50px / 1440px * 100vw);
    line-height: calc(23px / 1440px * 100vw);
  }

  .closing {
    max-width: calc(240px / 1440px * 100vw);
    margin-left: calc(520px / 1440px * 100vw);
    margin-top: calc(-50px / 1440px * 100vw);
    line-height: calc(23px / 1440px * 100vw);
  }

  h1 {
    will-change: transform;
    position: relative;
    // top: 10%;
    z-index: 2;
    display: flex;
    flex-direction: column;
    // align-items: center;
    justify-content: center;
    row-gap: calc(20px / 1440px * 100vw);
    font-weight: normal;
    text-transform: uppercase;
    font-size: calc(128px / 1440px * 100vw);
    line-height: calc(78px / 1440px * 100vw);
    margin: 0;
    margin-left: calc(216px / 1440px * 100vw);
    pointer-events: none;
    letter-spacing: calc(-4px / 1440px * 100vw);

    @-moz-document url-prefix() {
      div {
        padding-top: calc(10px / 1440px * 100vw);
      }
    }

    div {
      // color: red;
      // font-size: calc(128px / 1440px * 100vw);
      // line-height: calc(78px / 1440px * 100vw);
      display: inline-block;
      position: relative;
      height: calc(110px / 1440px * 100vw);
      overflow: hidden;
      // height: 110%;

      span {
        display: inline-block;
        transform: translateY(20%);
      }

      // &:after {
      //   content: "";
      //   position: absolute;
      //   left: 0;
      //   top: 0;
      //   width: 100%;
      //   height: 120%;
      //   background-color: black;
      //   // transform: translateY();
      // }
    }

    .colors {
      // color: yellow;
    }

    .and {
      margin-left: calc(290px / 1440px * 100vw);
    }

    .fabrics {
      margin-left: calc(480px / 1440px * 100vw);
    }
  }

  .fabricWrapper {
    perspective: 1000px;
  }

  div[data-fabric] {
    overflow: hidden;
  }

  img {
    width: 100%;
    transition: all 0.5s ease-out;

    &:hover {
      transform: scale(1.1);
    }
  }

  .mask {
    width: 104%;
    height: 104%;
    position: absolute;
    top: -2%;
    left: -2%;
    background-color: black;
  }

  .fabric1 {
    position: relative;

    width: calc(240px / 1440px * 100vw);
    left: calc(120px / 1440px * 100vw);
    margin-bottom: calc(-240px / 1440px * 100vw);
  }

  .fabric2 {
    position: relative;
    width: calc(354px / 1440px * 100vw);
    left: calc(240px / 1440px * 100vw);
    top: calc(50px / 1440px * 100vw);
  }

  .fabric3 {
    position: relative;
    width: calc(300px / 1440px * 100vw);
    left: calc(1020px / 1440px * 100vw);
    top: calc(-200px / 1440px * 100vw);
  }
  .fabric4 {
    position: relative;
    width: calc(223px / 1440px * 100vw);
    left: calc(140px / 1440px * 100vw);
    top: calc(200px / 1440px * 100vw);
  }
  .fabric5 {
    position: relative;
    width: calc(482px / 1440px * 100vw);
    left: calc(780px / 1440px * 100vw);
    top: calc(-200px / 1440px * 100vw);
  }
}

@media screen and (max-width: $md), (orientation: portrait) {
  .colorsAndFabric {
    .intro {
      max-width: calc(240px / $md * 100vw);
      margin-left: calc(450px / $md * 100vw);
      line-height: 23px;
    }

    .closing {
      max-width: calc(240px / $md * 100vw);
      margin-left: calc(520px / $md * 100vw);
      margin-top: calc(-50px / $md * 100vw);
      line-height: calc(23px / $md * 100vw);
    }

    h1 {
      flex-direction: column;
      row-gap: calc(0px / $md * 100vw);
      font-size: calc(105px / $md * 100vw);
      line-height: calc(70px / $md * 100vw);
      margin: 0;
      margin-left: calc(100px / $md * 100vw);
      letter-spacing: calc(-2px / $md * 100vw);
      margin-top: calc(250px / $md * 100vw);

      div {
        height: calc(100px / $md * 100vw);
      }

      .and {
        margin-left: calc(30px / $md * 100vw);
      }

      .fabrics {
        margin-left: calc(80px / $md * 100vw);
      }
    }

    img {
      width: 100%;
    }

    .fabric1 {
      width: calc(240px / $md * 100vw);
      left: 0;
      margin-bottom: calc(-360px / $md * 100vw);
      // margin-top: 0;
    }

    .fabric2 {
      position: relative;
      width: calc(354px / $md * 100vw);
      left: calc($md - 354px / $md * 100vw);
      top: calc(100px / $md * 100vw);
      // margin-top: 0;
    }

    .fabric3 {
      position: relative;
      width: calc(300px / $md * 100vw);
      left: calc(0px / $md * 100vw);
      top: calc(200px / $md * 100vw);
      // margin: 0;
    }
    .fabric4 {
      position: relative;
      width: calc(223px / $md * 100vw);
      left: calc($md - 223px / $md * 100vw);
      top: calc(0px / $md * 100vw);
    }
    .fabric5 {
      position: relative;
      width: calc(482px / $md * 100vw);
      left: calc(0px / $md * 100vw);
      top: calc(50px / $md * 100vw);
      // margin: 0;
    }
  }
}

@media screen and (max-width: $xs) {
  .colorsAndFabric {
    margin-top: 0vh;
    margin-top: 0svh;

    .intro {
      max-width: calc(240px / 375px * 100vw);
      // margin-left: calc(1020px / 375px * 100vw);
      // margin-bottom: calc(-50px / 375px * 100vw);
      margin: auto;
      // line-height: calc(23px / 375px * 100vw);
    }

    .closing {
      max-width: calc(240px / 375px * 100vw);
      margin-left: calc(70px / 375px * 100vw);
      margin-top: calc(100px / 375px * 100vw);
      line-height: calc(23px / 375px * 100vw);
    }

    h1 {
      flex-direction: column;
      row-gap: calc(0px / 375px * 100vw);
      font-size: calc(53px / 375px * 100vw);
      line-height: calc(56px / 375px * 100vw);
      margin: 0;
      margin-left: calc(30px / 375px * 100vw);
      letter-spacing: calc(-2px / 375px * 100vw);
      margin-top: calc(250px / 375px * 100vw);

      div {
        height: calc(60px / 375px * 100vw);
      }

      .and {
        margin-left: calc(30px / 375px * 100vw);
      }

      .fabrics {
        margin-left: calc(80px / 375px * 100vw);
      }
    }

    img {
      width: 100%;
    }

    .fabric1 {
      position: relative;

      width: calc(108px / 375px * 100vw);
      left: 0;
      margin-bottom: calc(-360px / 375px * 100vw);
      // top: 0;
    }

    .fabric2 {
      position: relative;
      width: calc(129px / 375px * 100vw);
      left: calc(375px - 129px / 375px * 100vw);
      top: 0;
    }

    .fabric3 {
      position: relative;
      width: calc(141px / 375px * 100vw);
      left: calc(0px / 375px * 100vw);
      top: calc(120px / 375px * 100vw);
      // margin: 0;
    }
    .fabric4 {
      position: relative;
      width: calc(117px / 375px * 100vw);
      left: calc(375px - 117px / 375px * 100vw);
      top: calc(0px / 375px * 100vw);
      // margin: 0;
    }
    .fabric5 {
      position: relative;
      width: calc(249px / 375px * 100vw);
      left: calc(0px / 375px * 100vw);
      top: calc(50px / 375px * 100vw);
    }
  }
}
