@import "./variables.scss";
@import "./fonts/HelveticaNeue.scss";

* {
  user-select: none;
}

body {
  margin: 0;
  background-color: $black;
  font-family: $helvetica;
  color: $white;
  overflow-x: hidden;
  overflow-y: hidden;
  // pointer-events: none;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

#canvas-container {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  height: 100lvh;
}

.yellow {
  color: $yellow;
}

.salmon {
  color: $salmon;
}

.red {
  color: $red;
}

@media screen and (max-width: $xs) {
  .it {
    #reinvented {
      font-size: calc(46px / 375px * 100vw);
      line-height: calc(30px / 375px * 100vw);
    }
  }
}
