@import "../../styles/variables.scss";

.biography {
  position: relative;
  width: 100%;

  .intro {
    text-transform: uppercase;
    max-width: calc(1080px / 1440px * 100vw);
    font-size: calc(108px / 1440px * 100vw);
    line-height: calc(108px / 1440px * 100vw);
    margin: 0;
    margin-left: calc(120px / 1440px * 100vw);
  }

  .alt {
    max-width: calc(542px / 1440px * 100vw);
    line-height: calc(23px / 1440px * 100vw);
    margin-top: calc(180px / 1440px * 100vw);
    margin-left: calc(778px / 1440px * 100vw);
    margin-bottom: calc(140px / 1440px * 100vw);
  }

  .imgWrapper {
    overflow: hidden;

    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: $md), (orientation: portrait) {
  .biography {
    position: relative;
    width: 100%;

    .intro {
      text-transform: uppercase;
      max-width: calc(680px / $md * 100vw);
      font-size: calc(108px / $md * 100vw);
      line-height: calc(108px / $md * 100vw);
      margin: 0;
      margin-left: calc(30px / $md * 100vw);
      word-wrap: break-word;
    }

    .alt {
      font-size: 16px;
      line-height: 23px;
      max-width: calc(350px / $md * 100vw);
      margin: auto;
      margin-top: calc(140px / $md * 100vw);
      margin-bottom: calc(140px / $md * 100vw);
    }

    .imgWrapper {
      overflow: hidden;

      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: $xs) {
  .biography {
    position: relative;

    .intro {
      text-transform: uppercase;
      width: calc(320px / 375px * 100vw);
      word-wrap: break-word;
      font-size: calc(47px / 375px * 100vw);
      line-height: calc(53px / 375px * 100vw);
      margin: auto;
    }

    .alt {
      // font-size: 14px;
      max-width: calc(269px / 375px * 100vw);
      // line-height: calc(23px / 375px * 100vw);
      margin-top: calc(140px / 375px * 100vw);
      margin-left: calc(70px / 375px * 100vw);
      margin-bottom: calc(140px / 375px * 100vw);
    }

    img {
      width: 100%;
    }
  }
}
