.switchLang {
  text-transform: uppercase;
  position: relative;
  cursor: pointer;
  padding: 5px;
  text-align: center;
  width: 30px;
  opacity: 0.5;
  transition: opacity 0.4s ease-in;

  &:hover {
    opacity: 1;
  }
}

.hiddenLang {
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  opacity: 0;
  transition: opacity 0.4s ease-in;
  padding: 5px;
}

.showLang {
  opacity: 1;
}
