@import "../../styles/variables.scss";

.nav {
  position: fixed;
  width: calc(100% - 100px);
  top: 40px;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  padding: 0 50px;
}

@media screen and (max-width: $sm) {
  .nav {
    top: 20px;
    width: calc(100% - 50px);
    padding: 0 25px;
  }
}
