@import "../../styles/variables.scss";

.loader {
  width: 100%;
  max-width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: calc(100px / 1440px * 100vw);
  background-color: black;
  z-index: 10;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -65%);
  overflow: hidden;
}

.entry {
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  color: white;
  outline: none;
  border: none;
  font-family: $helvetica;
  font-size: 16px;
  text-transform: uppercase;
  padding: 10px 30px;
  border-radius: 20px;
  font-weight: bold;
}

@media screen and (max-width: $xs) {
  .loader {
    font-size: calc(40px / 375px * 100vw);
  }
}
