@import "../../styles/variables.scss";

.wrapper {
  position: relative;
  left: calc(112px / 1440px * 100vw);
  margin-top: 25vh;
  margin-top: 25svh;

  .elleMurmura {
    font-size: 14px;
    opacity: 0.6;
  }

  .question {
    font-weight: normal;
    font-size: calc(34px / 1440px * 100vw);
    margin-top: calc(12px / 1440px * 100vw);
  }

  .paragraph {
    max-width: calc(300px / 1440px * 100vw);
    line-height: calc(23px / 1440px * 100vw);
    opacity: 0.7;
  }
}

@media screen and (max-width: $md), (orientation: portrait) {
  .wrapper {
    left: calc(60px / $md * 100vw);

    .question {
      font-size: calc(34px / $md * 100vw);
      margin-top: calc(12px / $md * 100vw);
    }

    .paragraph {
      max-width: calc(300px / $md * 100vw);
      line-height: calc(23px / $md * 100vw);
    }
  }
}

@media screen and (max-width: $xs) {
  .wrapper {
    left: calc(30px / 375px * 100vw);

    .question {
      font-size: calc(34px / 375px * 100vw);
      margin-top: calc(12px / 375px * 100vw);
    }
    .paragraph {
      max-width: calc(300px / 375px * 100vw);
      line-height: calc(23px / 375px * 100vw);
    }
  }
}
