@import "../../styles/variables.scss";

.collection {
  // border-top: 1px solid red;
  // border-bottom: 1px solid red;
  // margin-top: -50svh;
  position: relative;
  height: 500vh;
  height: 500svh;

  .inner {
    height: 100vh;
    height: 100svh;
    overflow: hidden;

    top: 0;
    position: sticky;
    display: flex;
    align-items: center;
  }

  h1 {
    font-size: calc(400px / 1440px * 100vw);
    font-weight: normal;
    white-space: nowrap;
    line-height: calc(320px / 1440px * 100vw);
    padding-right: 100px;
    transform: translateX(100%);
    margin: 0;
    font-family: $helveticaNeue;
    letter-spacing: calc(-16px / 1440px * 100vw);

    span {
      opacity: 0.1;
      display: inline-block;
    }

    .space {
      margin: calc(50px / 1440px * 100vw);
    }
  }

  .furnitures {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    // column-gap: calc( / 1440px * 100vw);
    column-gap: 10vw;
    // padding-left: 25vw;
    // justify-content: space-between;
    // background-color: red;
    height: 100vh;
    height: 100svh;

    img {
      width: calc(550px / 1440px * 100vw);
    }
  }
}

@media screen and (max-width: $md), (orientation: portrait) {
  .collection {
    h1 {
      font-size: calc(400px / $md * 100vw);
      font-weight: normal;
      white-space: nowrap;
      line-height: calc(320px / $md * 100vw);
      padding-right: 100px;
      transform: translateX(100%);
      margin: 0;
      font-family: $helveticaNeue;
      letter-spacing: calc(-16px / $md * 100vw);

      span {
        opacity: 0.1;
        display: inline-block;
      }

      .space {
        margin: calc(50px / $md * 100vw);
      }
    }

    .furnitures {
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      column-gap: 10vw;

      img {
        width: calc(550px / $md * 100vw);
      }
    }
  }
}
