@import "../../styles/variables.scss";

.savoirfaire {
  //   height: 100vh;
  position: relative;
  z-index: 1;
  margin-top: 80svh;
  margin-top: 80vh;

  .aluminium {
    position: absolute;
    top: calc(800px / 1440px * 100vw);
    row-gap: calc(40px / 1440px * 100vw);
    display: flex;
    flex-direction: column;
    font-size: calc(168px / 1440px * 100vw);
    line-height: calc(88px / 1440px * 100vw);
    perspective: 1000px;
    // transform-style: preserve-3d;

    span {
      display: inline-block;
    }

    .a {
      margin-top: calc(250px / 1440px * 100vw);
      margin-left: calc(180px / 1440px * 100vw);
      margin-bottom: calc(80px / 1440px * 100vw);
    }
    .l {
      margin-left: calc(1000px / 1440px * 100vw);
      margin-bottom: calc(260px / 1440px * 100vw);
    }
    .firstU {
      margin-left: calc(440px / 1440px * 100vw);
      margin-bottom: calc(220px / 1440px * 100vw);
    }
    .middleM {
      margin-left: calc(180px / 1440px * 100vw);
      margin-bottom: calc(190px / 1440px * 100vw);
    }
    .firstI {
      margin-left: calc(1200px / 1440px * 100vw);
      margin-bottom: calc(150px / 1440px * 100vw);
    }
    .n {
      margin-left: calc(700px / 1440px * 100vw);
      margin-bottom: calc(150px / 1440px * 100vw);
    }
    .secondI {
      margin-left: calc(1000px / 1440px * 100vw);
      margin-bottom: calc(100px / 1440px * 100vw);
    }
    .secondU {
      margin-left: calc(440px / 1440px * 100vw);
      margin-bottom: calc(140px / 1440px * 100vw);
    }

    .secondM {
      margin-left: calc(950px / 1440px * 100vw);
      margin-top: calc(80px / 1440px * 100vw);
    }
  }

  h1 {
    display: flex;
    flex-direction: column;
    row-gap: calc(20px / 1440px * 100vw);
    text-transform: uppercase;
    font-weight: normal;
    font-size: calc(128px / 1440px * 100vw);
    margin: 0;
    margin-left: calc(120px / 1440px * 100vw);
    line-height: calc(78px / 1440px * 100vw);
    letter-spacing: calc(-4px / 1440px * 100vw);
    perspective: 1000px;
    // transform-style: preserve-3d;

    .anUnique,
    .savoir,
    .faire {
      overflow: hidden;
      height: calc(100px / 1440px * 100vw);
      padding-top: calc(12px / 1440px * 100vw);
      // background-color: red;

      span {
        display: inline-block;
        transform: translateY(15%);
      }
    }

    .anUnique {
      display: flex;
      flex-direction: column;
      row-gap: calc(60px / 1440px * 100vw);
      padding-top: calc(15px / 1440px * 100vw);

      height: calc(250px / 1440px * 100vw);
      // background-color: red;

      .unique {
        // margin-left: calc(30px / 1440px * 100vw);
      }
    }

    .savoir {
      margin-left: calc(474px / 1440px * 100vw);
    }

    .faire {
      margin-left: calc(775px / 1440px * 100vw);
    }
  }

  p {
    font-size: calc(16px / 1440px * 100vw);
    line-height: calc(23px / 1440px * 100vw);
    position: relative;
    z-index: 2;
  }

  img {
    width: 100%;
    position: relative;
  }

  .intro {
    max-width: calc(284px / 1440px * 100vw);
    margin: 0;
    margin-left: calc(180px / 1440px * 100vw);
    margin-top: calc(60px / 1440px * 100vw);
  }

  .bring {
    max-width: calc(284px / 1440px * 100vw);
    margin: 0;
    margin-left: calc(1060px / 1440px * 100vw);
    margin-top: calc(-260px / 1440px * 100vw);
    // position: absolute;
    // right: calc(120px / 1440px * 100vw);
  }

  [data-img] {
    perspective: 1000px;
    // transform-style: preserve-3d;
  }

  .img1 {
    width: calc(301px / 1440px * 100vw);
    margin-left: calc(595px / 1440px * 100vw);
    position: relative;
    z-index: -1;
  }

  .specialist {
    max-width: calc(244px / 1440px * 100vw);
    margin-left: calc(544px / 1440px * 100vw);
    margin-top: calc(300px / 1440px * 100vw);
    // visibility: hidden;
  }

  .img2 {
    width: calc(301px / 1440px * 100vw);
    margin-left: calc(290px / 1440px * 100vw);
    margin-top: calc(-120px / 1440px * 100vw);
    position: relative;
    z-index: -1;
  }

  .molten {
    max-width: calc(258px / 1440px * 100vw);
    margin-left: calc(720px / 1440px * 100vw);
    margin-top: calc(110px / 1440px * 100vw);
    // visibility: hidden;
  }

  .img3 {
    width: calc(480px / 1440px * 100vw);
    margin-left: calc(840px / 1440px * 100vw);
    margin-top: calc(-330px / 1440px * 100vw);
    position: relative;
    z-index: -1;
  }

  .img4 {
    width: calc(301px / 1440px * 100vw);
    margin-left: calc(240px / 1440px * 100vw);
    margin-top: calc(-50px / 1440px * 100vw);
    position: relative;
    z-index: -1;
  }

  .cleansed {
    max-width: calc(301px / 1440px * 100vw);
    margin-left: calc(300px / 1440px * 100vw);
    margin-top: calc(60px / 1440px * 100vw);
    // visibility: hidden;
  }

  .img5 {
    width: calc(420px / 1440px * 100vw);
    margin-left: calc(815px / 1440px * 100vw);
    margin-top: calc(50px / 1440px * 100vw);
    position: relative;
    z-index: -1;
  }
  .longevity {
    max-width: calc(301px / 1440px * 100vw);
    margin-left: calc(240px / 1440px * 100vw);
    margin-top: calc(-40px / 1440px * 100vw);
    // visibility: hidden;
  }
}

