.toggle {
  position: fixed;
  top: 0;
  opacity: 0;
  color: white;
  padding: 8px 16px;
  background-color: black;
  border-radius: 20px;
  z-index: 10;
  font-size: 14px;
  transition: opacity 0.5s ease-out 0s;

  // display: flex;
  // align-items: center;
  // column-gap: 10px;
}

.hasMoved {
  opacity: 0.5;
}

.hasClicked {
  pointer-events: none;
}

.isHoveringTerms {
  opacity: 0;
}
