@import "../../styles/variables.scss";

.discover {
  // border-top: 1px solid red;
  // border-bottom: 1px solid red;
  position: relative;
  height: 500vh;
  height: 500svh;
  z-index: -10;

  .inner {
    overflow: hidden;
    height: 100vh;
    height: 100svh;
    top: 0;
    // bottom: 0;
    position: sticky;
    display: flex;
    align-items: center;
    perspective: 1000px;
    transform-style: preserve-3d;
    // justify-content: center;
  }

  h1 {
    margin: 0;
    // opacity: 0.14;
    font-size: calc(400px / 1440px * 100vw);
    line-height: calc(320px / 1440px * 100vw);
    font-weight: normal;
    white-space: nowrap;
    padding-right: calc(100px / 1440px * 100vw);
    position: relative;
    transform: translateX(100%);
    font-family: $helveticaNeue;
    letter-spacing: calc(-16px / 1440px * 100vw);
    padding-bottom: calc(50px / 1440px * 100vw);
    // z-index: -1;
    // position: relative;
    // z-index: -10;

    span {
      // opacity: 0.1;
      display: inline-block;
    }

    .space {
      margin: calc(50px / 1440px * 100vw);
    }
  }

  @-moz-document url-prefix() {
    h1 {
      padding-bottom: calc(30px / 1440px * 100vw);
    }
  }
}

@media screen and (max-width: $md), (orientation: portrait) {
  .discover {
    height: 500vh;
    height: 500svh;
    h1 {
      font-size: calc(300px / $md * 100vw);
      line-height: calc(240px / $md * 100vw);
      padding-right: calc(100px / $md * 100vw);
      letter-spacing: calc(-8px / $md * 100vw);

      .space {
        margin: calc(25px / $md * 100vw);
      }
    }

    @-moz-document url-prefix() {
      h1 {
        padding-bottom: calc(30px / $md * 100vw);
      }
    }
  }
}

@media screen and (max-width: $xs) {
  .discover {
    height: 400vh;
    height: 400svh;
    h1 {
      font-size: calc(200px / $xs * 100vw);
      line-height: calc(240px / $xs * 100vw);
      padding-right: calc(100px / $xs * 100vw);
      letter-spacing: calc(-8px / $xs * 100vw);

      .space {
        margin: calc(25px / $xs * 100vw);
      }
    }

    @-moz-document url-prefix() {
      h1 {
        padding-bottom: calc(30px / $xs * 100vw);
      }
    }
  }
}
