@import "../../styles/variables.scss";

.wrapper {
  height: 130vh;
  height: 130svh;

  h1 {
    margin: 0;
    display: flex;
    flex-direction: column;
    row-gap: calc(18px / 1440px * 100vw);
    font-size: calc(128px / 1440px * 100vw);
    font-weight: normal;
    line-height: calc(78px / 1440px * 100vw);
    position: absolute;
    left: calc(112px / 1440px * 100vw);
    top: calc(180px / 1440px * 100vw);
    letter-spacing: calc(-5px / 1440px * 100vw);

    div {
      // overflow: hidden;
      // background-color: red;
      // padding-top: calc(15px / 1440px * 100vw);
    }

    span {
      // transform-origin: center;
      display: inline-block;
      transform: translateY(15%);
    }
  }

  @-moz-document url-prefix() {
    h1 {
      div {
        padding-top: calc(10px / 1440px * 100vw);
      }
    }

    .reinvented {
      padding-bottom: calc(10px / 1440px * 100vw);
    }
  }
}

.intro {
  font-style: italic;
  font-size: calc(18px / 1440px * 100vw);
  font-family: $helveticaNeue;
  margin-bottom: calc(36px / 1440px * 100vw);
}

.description {
  display: flex;
  flex-direction: column;
  // row-gap: 45px;
  max-width: calc(335px / 1440px * 100vw);
  position: absolute;
  right: calc(147px / 1440px * 100vw);
  top: calc(566px / 1440px * 100vw);

  .paragraph {
    margin: 0;
    font-family: $helveticaNeue;
    line-height: calc(23px / 1440px * 100vw);
    margin-bottom: calc(45px / 1440px * 100vw);
  }

  .subtitle {
    margin: 0;
    // position: relative;
    // top: calc(10px / 1440px * 100vw);
    font-size: calc(20px / 1440px * 100vw);
    line-height: calc(27px / 1440px * 100vw);
    margin-bottom: calc(12px / 1440px * 100vw);
  }
}

.dior {
  height: calc(100px / 1440px * 100vw);
  overflow: hidden;
}

.reinvented {
  margin-left: calc(108px / 1440px * 100vw);
  display: flex;
  align-items: flex-start;
  height: calc(100px / 1440px * 100vw);
  overflow: hidden;
  // padding-bottom: calc(10px / 1440px * 100vw);
}

.by {
  margin-left: calc(30px / 1440px * 100vw);
  padding-right: calc(5px / 1440px * 100vw);
  // height: calc(100px / 1440px * 100vw);
}

.philippe {
  height: calc(100px / 1440px * 100vw);
  overflow: hidden;
}

.starck {
  overflow: hidden;
  margin-left: calc(158px / 1440px * 100vw);
  height: calc(100px / 1440px * 100vw);
}

@media screen and (max-width: $lg) {
  .wrapper {
    h1 {
      top: calc(180px / $lg * 100vw);
    }
  }
}

@media screen and (max-width: $md), (orientation: portrait) {
  .wrapper {
    height: 150vh;
    height: 150svh;

    h1 {
      font-size: calc(90px / $md * 100vw);
      line-height: calc(60px / $md * 100vw);
      letter-spacing: calc(-2px / $md * 100vw);
      top: calc(170px / $md * 100vw);
    }
  }

  .intro {
    font-size: calc(18px / $md * 100vw);
    margin-bottom: calc(36px / $md * 100vw);
  }

  .description {
    display: flex;
    flex-direction: column;
    // row-gap: 25px;
    max-width: calc(306px / $md * 100vw);
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: calc(750px / $md * 100vw);

    .paragraph {
      font-size: 16px;
      line-height: 23px;
      margin-bottom: calc(45px / $md * 100vw);
    }

    .subtitle {
      font-size: 20px;
      line-height: 27px;
      margin-bottom: 12px;
    }
  }

  .dior {
    height: calc(85px / $md * 100vw);
    line-height: calc(70px / $md * 100vw);
  }

  .reinvented {
    padding-right: calc(2px / $md * 100vw);
    height: calc(170px / $md * 100vw);
    margin-left: 0;
    flex-direction: column;
    // row-gap: 10px;
    line-height: calc(80px / $md * 100vw);
    margin-bottom: calc(10px / $md * 100vw);
    padding-bottom: calc(10px / $md * 100vw);
  }

  .by {
    margin-left: 0;
    position: relative;
    align-self: flex-end;
    margin-top: calc(10px / $md * 100vw);
  }

  .philippe {
    height: calc(85px / $md * 100vw);
  }

  .starck {
    padding-top: 5px;
    padding-right: calc(5px / $md * 100vw);
    height: calc(85px / $md * 100vw);
    margin-left: calc(90px / $md * 100vw);
  }
}

@media screen and (max-width: $xs) {
  .wrapper {
    height: 200vh;
    height: 200svh;

    h1 {
      font-size: calc(53px / 375px * 100vw);
      line-height: calc(45px / 375px * 100vw);
      letter-spacing: calc(-2px / 375px * 100vw);
      top: calc(170px / 375px * 100vw);
    }
  }

  .intro {
    font-size: calc(18px / 375px * 100vw);
    margin-bottom: calc(36px / 375px * 100vw);
  }

  .description {
    display: flex;
    flex-direction: column;
    row-gap: 25px;
    max-width: calc(306px / 375px * 100vw);
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: calc(566px / 375px * 100vw);

    .paragraph {
      margin: 0;
      font-size: 14px;
    }
  }

  .dior {
    height: calc(50px / 375px * 100vw);
  }

  .reinvented {
    padding-right: calc(2px / 375px * 100vw);
    height: calc(110px / 375px * 100vw);
    margin-left: 0;
    flex-direction: column;
    line-height: calc(50px / 375px * 100vw);
    margin-bottom: calc(10px / 375px * 100vw);
    padding-bottom: calc(10px / 375px * 100vw);
  }

  .by {
    margin-left: 0;
    position: relative;
    right: calc(30px / 375px * 100vw);
    align-self: flex-end;
    margin-top: calc(10px / 375px * 100vw);
    // padding-bottom: 10px;
  }

  .philippe {
    height: calc(50px / 375px * 100vw);
  }

  .starck {
    padding-right: calc(5px / 375px * 100vw);
    height: calc(50px / 375px * 100vw);
    margin-left: calc(90px / 375px * 100vw);
  }
}
